.aboutContainer {
    display: grid;
    grid-template-columns: 100%;
    gap: 15%;
}

.aboutMe {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}

.aboutMeImage {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.justifyContent {
    text-align: justify
}

.aboutMeImage:hover {
    transform: rotate(0);
}

.aboutCards {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.aboutCard {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    width: 300px;
    text-align: center;
    transition: var(--transition);
}

.aboutCard:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.aboutIcon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.aboutCard h5 {
    font-size: 0.95rem;
}

.aboutCard small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.aboutContent p {
    color: var(--color-light);
    margin: 2rem 0 2.6rem;
}

/* =========== MEDIA QUERIES (Medium devices) ========== */

@media screen and (max-width: 1024px) {
    .aboutContainer {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .aboutMe {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .aboutContent p {
        margin: 2rem 0 1.5rem;
    }
}

/* =========== MEDIA QUERIES (Small devices) ========== */

@media screen and (max-width: 600px) {
    .aboutMe {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .aboutCards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .aboutContent {
        text-align: center;
    }

    .aboutContent p {
        margin: 1.5rem 0;
    }
}