header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.headerContainer {
    text-align: center;
    height: auto;
    position: relative;
}

/* =============== CTA ============== */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* =============== Header Socials ============== */

.headerSocials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.2rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.headerSocials::after {
    content: ' ';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* =============== Header Socials ============== */

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 0 1.5rem 1.5rem 1.5rem;
}

/* =============== Scroll Down ============== */

.scrollDown {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    font-weight: 300;
    font-size: 1.5rem;
}

/* =========== MEDIA QURIES (Medium devices) ========== */

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

/* =========== MEDIA QURIES (Small devices) ========== */

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .headerSocials,
    .scrollDown {
        display: none;
    }
}